<template>
	<div class="users">
		<el-table :data="users" style="width: 100%" max-height="800" stripe border>
			<el-table-column prop="userid" label="用户id" width="100" fixed>
			</el-table-column>
			<el-table-column prop="username" label="用户名" width="200">
			</el-table-column>
			<el-table-column prop="password" label="密码" width="150">
			</el-table-column>
			<el-table-column label="管理员" width="200">
				<template slot-scope="scope">
					<el-tag type="success" v-if="scope.row.admins==1">管理员</el-tag>
					<el-tag type="info" v-else-if="scope.row.admins==0">普通用户</el-tag>
				</template>
			</el-table-column>
			<el-table-column prop="" label="操作" width="200">
				<template slot-scope="scope">
					<div style="display: flex;">
						<el-button size="mini" @click="change(scope)">修改</el-button>
						<el-button size="mini" type="danger" @click="del(scope)">删除</el-button>
					</div>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog title="修改" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
			<el-form class="form" ref="form" :model="changeData" label-width="60px">
				<el-form-item label="用户名">
					<el-input v-model="changeData.username"></el-input>
				</el-form-item>
				<el-form-item label="密码">
					<el-input v-model="changeData.password"></el-input>
				</el-form-item>
				<el-form-item label="权限">
					<el-radio v-model="changeData.admins" :label="1">管理员</el-radio>
					<el-radio v-model="changeData.admins" :label="0">普通用户</el-radio>
				</el-form-item>

			</el-form>
			<el-button type="primary" @click="save(index)">保存</el-button>

		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "",
		components: {

		},
		props: {

		},
		data() {
			return {
				users: [],
				dialogVisible: false,
				index: null,
				changeData: {}
			}
		},
		computed: {

		},
		methods: {
			getall() {
				this.$axios({
					url: "/zone/action/v1/getalluser",
					method: "post",
					data: this.$admin.form
				}).then(res => {
					let data = res.data
					if (data.RESULT == 'T') {
						this.users = data.ROWS_DETAIL
					}
				})
			},
			change(scope) {
				this.index = scope.$index
				this.dialogVisible = true
				this.changeData = JSON.parse(JSON.stringify(scope.row))

			},
			save(index) {
				if (this.$admin.form.userid == this.changeData.userid &&
					this.changeData.admins != 1
				) {
					this.$message.warning('不能更改自己的管理员权限')
					return
				}
				this.$axios({
					url: "/zone/action/v1/alteruser",
					method: "post",
					data: this.changeData
				}).then(res => {
					let data = res.data
					if (data.RESULT == 'T') {
						this.$set(this.users, index, this.changeData)
						this.dialogVisible = false
					}
					this.$message.info(data.MESSAGE)
				})


			},
			del(scope) {
				if (this.$admin.form.userid == scope.row.userid) {
					this.$message.warning('不能删除自己')
					return
				}
				this.$axios({
					url: "/zone/action/v1/deleteuser",
					method: "post",
					data: {
						userid: scope.row.userid
					}
				}).then(res => {
					let data = res.data
					if (data.RESULT == 'T') {
						this.users.splice(scope.$index, 1)
					}
					this.$message.info(data.MESSAGE)
				})


			},
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(() => {
						done();
					})
					.catch(() => {});
			}

		},
		created() {
			this.getall()
		}
	}
</script>

<style scoped>
	.users {
		margin: 10px;
	}
</style>
