<template>
	<el-form class="form" ref="form" :model="form" label-width="60px">
		<h3>管理系统登陆</h3>

		<el-form-item label="账号">
			<el-input v-model="form.userid"></el-input>
		</el-form-item>
		<el-form-item label="密码">
			<el-input v-model="form.password" type="password" show-password></el-input>
		</el-form-item>

		<el-button type="primary" @click="onSubmit">登陆</el-button>

	</el-form>
</template>

<script>
	export default {
		name: "",

		data() {
			return {
				form: {
					userid: '',
					password: '',
				}
			}
		},
		methods: {
			onSubmit() {
				let form = {
					"userid": this.form.userid,
					"password": this.form.password,
				}
				this.$axios({
					url: "/zone/action/v1/login",
					method: "post",
					data: form
				}).then(res => {
					let data = res.data

					if (data.RESULT == 'F') {
						this.$message.error(data.Message)
						return
					}
					if (data.admin == 0) {
						this.$message.error("非管理员登陆")
						return
					}
					if (data.RESULT == 'T' && data.admin == 1) {
						this.$message.success(data.MESSAGE)
						this.login(form)
					}
				})
			},
			login(form) {
				this.$admin.form = form
				console.log('admin',this.$admin)
				this.$emit('setSign')
			}
		},
		created() {
			// let form = {
			// 	"userid": this.form.userid,
			// 	"password": this.form.password,
			// }
			// this.login(form)
		}
	}
</script>

<style scoped>
	.form {
		margin: auto;
		margin-top: 50px;
		padding: 30px;
		width: 400px;
		box-shadow: 1px 1px 8px #2C3E50;
		text-align: center;
	}
</style>
