<template>
	<div>
		<el-table :data="shares" style="width: 100%" max-height="800" stripe border>
			<el-table-column prop="id" label="id" width="50" fixed>
			</el-table-column>
			<el-table-column prop="title" label="标题" width="150">
			</el-table-column>
			<el-table-column prop="image" label="缩略图">
				<template slot-scope="scope">
					<img :src="scope.row.image" class="img" />
				</template>
			</el-table-column>
			<el-table-column prop="author" label="作者id" width="100">
			</el-table-column>
			<el-table-column prop="checkon" label="审核状态" width="100">
				<template slot-scope="scope">
					<el-tag v-if="scope.row.checkon==0">未审核</el-tag>
					<el-tag type="success" v-else-if="scope.row.checkon==1">通过</el-tag>
					<el-tag type="info" v-else-if="scope.row.checkon==2">未通过</el-tag>
				</template>
			</el-table-column>
			<el-table-column prop="checkon" label="操作" width="150">
				<template slot-scope="scope">
					<el-dropdown :hide-on-click="false" @command="checkshare">
						<span class="el-dropdown-link">
							操作项<i class="el-icon-arrow-down el-icon--right"></i>
						</span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item :disabled="scope.row.checkon!=0" :command="[scope.row,1]">审核通过
							</el-dropdown-item>
							<el-dropdown-item :disabled="scope.row.checkon!=0" :command="[scope.row,2]">审核不通过
							</el-dropdown-item>
							<el-dropdown-item divided></el-dropdown-item>
							<el-dropdown-item :command="[scope,3]">删除该分享</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default {
		name: 'share',
		data() {
			return {
				shares: []
			}
		},

		methods: {
			getall() {
				this.$axios({
					url: "/zone/action/v2/getallshare",
					method: "post",
					data: this.$admin.form
				}).then(res => {
					let data = res.data
					if (data.RESULT == 'T') {
						this.shares = data.ROWS_DETAIL
					}
				})
			},
			checkshare(args) {
				if (args[1] == 1 || args[1] == 2) {
					this.$axios({
						url: "/zone/action/v2/checkshare",
						method: "post",
						data: {
							id: args[0].id,
							checkon: args[1],
							userid: this.$admin.form.userid
						}
					}).then(res => {
						let data = res.data
						if (data.RESULT == 'T') {
							args[0].checkon = args[1]
						}
					})
				} else if (args[1] == 3) {
					this.$axios({
						url: "/zone/action/v2/deleteshare",
						method: "post",
						data: {
							id: args[0].row.id,
							author: args[0].row.author
						}
					}).then(res => {
						let data = res.data
						if (data.RESULT== 'T'){
							this.shares.splice(args[0].$index,1)
						}
						this.$message.info(data.MESSAGE)
					})
				}
			}
		},
		created() {
			this.getall()
		}
	}
</script>

<style scoped>
	.img {
		max-height: 150px;
	}

	.el-dropdown-link {
		cursor: pointer;
		color: #409EFF;
	}
</style>
