<template>
	<div class="index">
		<menus @setIndex="setIndex"></menus>
		<div class="content">
			<div class="time">
				<div>管理员</div>
				<div>{{time}}</div>

			</div>
			<hr>
			<div v-if="index==1">首页</div>
			<share class="share" v-if="index==2"></share>
			<usermanage v-if="index==3"></usermanage>
		</div>
	</div>
</template>

<script>
	import menus from './menu.vue'
	import share from './share.vue'
	import usermanage from './usermanage.vue'
	export default {
		name: "",
		components: {
			menus,
			share,usermanage
		},
		data() {
			return {
				index: 3,
				time: new Date().toLocaleString()
			}
		},
		methods: {
			setIndex(index) {
				this.index = index
			},
			newtime() {
				this.time = new Date().toLocaleString()
			}
		},
		created() {
			setInterval(this.newtime, 1000)
		}
	}
</script>

<style scoped>
	.index {
		display: flex;
	}

	.content {
		width: 100vw;
	}

	.time {
		margin: 10px;
		display: flex;
		justify-content: space-between;
	}

	.share {
		margin: 10px;
	}
</style>
